<template>
  <div class="unlash-page">
    <h1>Password Generate Unlash</h1>
    <Form :label-width="80">
      <FormItem label="password">
        <Input
          v-model="password"
          placeholder="password"
          type="password"
          size="large"
          @on-keydown="keyDown"/>
      </FormItem>
      <FormItem label="input">
        <Input
          v-model="value"
          placeholder="input"
          size="large"
          @on-keydown="keyDown"/>
      </FormItem>
    </Form>
    <h1>{{code}}</h1>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import clipboardPolyfill from 'clipboard-polyfill'
import { generator } from '@/lib/tool.js'
export default {
  data () {
    return {
      password: '',
      value: '',
      code: ''
    }
  },
  mounted () {
    this.password = Cookies.get('password-generate') || ''
  },
  methods: {
    keyDown (e) {
      if (e.code === 'Enter') {
        this.generate()
      }
    },
    generate () {
      this.code = generator(this.value, this.password)
      clipboardPolyfill.writeText(this.code)
      this.$Message.success(`${this.code} 已经复制到剪贴板`)
    }
  }
}
</script>

<style lang="scss">
.unlash-page{
  margin: 0px 100px;
  h1{
    line-height: 100px;
    text-align: center;
  }  
}
</style>



