<template>
  <div class="index-page">
    <h1>Password Generate</h1>
    <Row :gutter="10">
      <!-- left -->
      <Col span="6">
        <card dis-hover>
          <p slot="title">设置项</p>
          <Form :label-width="80">
            <FormItem label="导入">
              <Upload
                :before-upload="handleUpload"
                action="default">
                <Button type="ghost" icon="ios-cloud-upload-outline">CSV</Button>
              </Upload>
            </FormItem>
            <FormItem label="日期"><DatePicker type="date" v-model="form.date"></DatePicker></FormItem>
            <FormItem label="前缀"><Input v-model="form.prefix"></Input></FormItem>
            <FormItem label="备注"><Input v-model="form.note" @on-keydown="keyDown"></Input></FormItem>
            <FormItem label="简单秘钥">
              <Input v-model="form.password">
                <Button slot="append" @click="Cookies.set('password-generate', form.password)">保存</Button>
              </Input>
            </FormItem>
            <FormItem>
              <Button type="primary" size="large" long @click="generate">生成</Button>
            </FormItem>
          </Form>
        </card>
      </Col>
      <!-- right -->
      <Col span="18">
        <card dis-hover>
          <p slot="title">生成记录</p>
          <ButtonGroup slot="extra">
            <Button size="small" @click="table.data = []">清空</Button>
            <Button size="small" @click="$refs.table.exportCsv()">导出CSV</Button>
          </ButtonGroup>
          <div style="margin: -17px">
            <TableBase :data="table.data" ref="table"></TableBase>
          </div>
        </card>
      </Col>
    </Row>
  </div>
</template>

<script>
import moment from '@/assets/moment.js'
import Cookies from 'js-cookie'
import papa from 'papaparse'
import clipboardPolyfill from 'clipboard-polyfill'
import { generator } from '@/lib/tool.js'
import TableBase from '@/components/table/tableBase.vue'
export default {
  components: {
    TableBase
  },
  data () {
    return {
      form: {
        date: new Date(),
        password: '',
        prefix: '归档 - ',
        note: ''
      },
      table: {
        data: []
      },
      Cookies
    }
  },
  mounted () {
    const password = Cookies.get('password-generate')
    this.form.password = password || ''
  },
  methods: {
    keyDown (e) {
      if (e.code === 'Enter') {
        this.generate()
      }
    },
    handleUpload (file) {
      papa.parse(file, {
        complete: (results, file) => {
          if (results.errors.length > 0) {
            this.$Message.error('解析错误')
          } else {
            const [, ...res] = results.data.filter(e => {
              return e[0] && e[1]
            }).map(e => ({
              fileName: e[0],
              code: e[1]
            }))
            this.table.data = [
              ...res,
              ...this.table.data
            ]
          }
        }
      })
      return false
    },
    generate () {
      const date = moment(this.form.date).format('YYYY年MMMMDodddd')
      const prefix = this.form.prefix
      const note = this.form.note ? ` [ ${this.form.note} ]` : ''
      const fileName = `${prefix}${date}${note}`
      const code = generator(fileName, this.form.password)
      // 检查是否有简单秘钥
      if (!this.form.password) {
        this.$Notice.error({
          title: '没有设置简单秘钥'
        })
        return
      }
      // 检查是否有重复
      if (this.table.data.find(e => e.code === code)) {
        this.$Notice.error({
          title: '检测到生成重复的秘钥'
        })
        return
      }
      // 给表格添加数据
      this.table.data.unshift({
        fileName,
        code
      })
      // 复制
      clipboardPolyfill.writeText(code)
      this.$Message.success(`${code} 已经复制到剪贴板`)
    }
  }
}
</script>

<style lang="scss">
.index-page{
  margin: 0px 100px;
  h1{
    line-height: 100px;
    text-align: center;
  }  
}
</style>



