<template>
  <div>
    <Tag type="border">{{value}}</Tag>
    <Tooltip content="复制" placement="top">
      <Tag color="blue" @click.native="copy">
        <Icon type="ios-copy"></Icon>
      </Tag>
    </Tooltip>
  </div>
</template>

<script>
import clipboardPolyfill from 'clipboard-polyfill'
export default {
  props: {
    value: ''
  },
  methods: {
    copy () {
      clipboardPolyfill.writeText(this.value)
      this.$Message.success(`[${this.value}] 已经复制到剪贴板`)
    }
  }
}
</script>



