import Vue from 'vue'
import Router from 'vue-router'
import index from '@/page/index.vue'
import unlash from '@/page/unlash.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    }, {
      path: '/unlash',
      name: 'unlash',
      component: unlash
    }
  ]
})



