import md5 from 'js-md5'

export function generator (fileName = '', password = '') {
  // 计算密码
  // 文件名 + password-generate + 文件名倒序 + password
  let [...fileNameArray] = fileName
  let fileNameReverse = fileNameArray.reverse().join('')
  return md5(`${fileName}password-generate${fileNameReverse}${password}`)
}



