<template>
  <Table v-bind="table" :data="data" ref="table"></Table>
</template>

<script>
export default {
  props: {
    data: {
      default () {
        return []
      }
    }
  },
  data () {
    return {
      table: {
        columns: [
          {
            title: '文件名',
            key: 'fileName',
            render: (h, params) => {
              return h('div', [
                h('TableCellCopy', {
                  props: {
                    value: params.row.fileName
                  }
                })
              ])
            }
          },
          {
            title: '秘钥',
            key: 'code',
            width: 330,
            render: (h, params) => {
              return h('div', [
                h('TableCellCopy', {
                  props: {
                    value: params.row.code
                  }
                })
              ])
            }
          }
        ],
        stripe: true,
        border: true,
        size: 'small'
      }
    }
  },
  methods: {
    exportCsv () {
      if (this.data.length === 0) {
        this.$Notice.error({
          title: '没有需要导出的数据'
        })
        return
      }
      this.$refs.table.exportCsv({
        filename: '密码'
      })
    }
  }
}
</script>






